.UserNatijaBlock {
   display: flex;
   align-items: center;

   &-num {
      width: 5%;
      display: flex;
      align-items: center;
      justify-content: center;
      border: 2px solid black;
      padding: 5px 0;
      font-size: 17px;
      border-top: none;

      @media screen and (max-device-width: 570px) {
         width: 15%;
         font-size: 12px;
      }
      @media screen and (max-device-width: 300px) {
         font-size: 10px;
      }
   }
   
   &-fullname {
      width: 35%;
      padding: 5px 0;
      padding-left: 10px;
      box-sizing: border-box;
      border: 2px solid black;
      font-size: 17px;
      border-top: none;
      border-left: none;
      text-decoration: none;
      color: black;
      font-weight: bold;
      text-align: center;

      @media screen and (max-device-width: 570px) {
         width: 60%;
         font-size: 12px;
      }

      @media screen and (max-device-width: 300px) {
         font-size: 10px;
      }
   }
   
   &-items {
      width: 20%;
      display: flex;
      @media screen and (max-device-width: 570px) {
         display: none;
      }
      
      &__fan {
         font-size: 17px;
         padding: 5px 10px;
         width: 50%;
         border-right: 2px solid black;
         border-bottom: 2px solid black;
         font-weight: normal;
         
      }
      
      &__ball {
         padding: 5px 10px;
         font-size: 17px;
         display: flex;
         align-items: center;
         justify-content: center;
         width: 50%;
         border-right: 2px solid black;
         border-bottom: 2px solid black;
      }
   }

   &-allBall {
      width: 20%;
      font-size: 17px;
      border-top: none;
      border-left: none;
      display: flex;
      align-items: center;
      justify-content: center;
      border-bottom: 2px solid black;
      border-right: 2px solid black;
      padding: 5px 10px;
      box-sizing: border-box;

      @media screen and (max-device-width: 570px) {
         width: 35%;
         font-size: 12px;
      }

      @media screen and (max-device-width: 300px) {
         font-size: 10px;
      }
   }
}