.login {
   position: fixed;
   top: 0;
   right: -100%;
   bottom: 0;
   width: 100%;
   height: 100vh;
   z-index: 99999999;
   background: rgba(0, 0, 0, 0.699);
   display: flex;
   align-items: center;
   justify-content: center;
   opacity: 0;
   transition: all ease 0.5s;
   
   &.active {
      opacity: 1;
      transition: all ease 0.5s;
      right: 0;
   }

   .formLogin {
      display: flex;
      flex-direction: column;
      background: white;
      padding: 20px 40px;
      border-radius: 20px;
      @media screen and (max-device-width: 570px) {
         padding: 10px 20px;
         border-radius: 5px;
      }

      h2 {
         @media screen and (max-device-width: 570px) {
            font-size: 20px;
         }
      }

      input {
         background: #F3F3F3;
         border-radius: 5px;
         font-size: 22px;
         padding: 5px 10px;
         outline: none;
         font-style: normal;
         font-weight: normal;
         color: #000000;
         border: none;
         margin-top: 20px;

         @media screen and (max-device-width: 570px) {
            font-size: 17px;
         }

         &:focus {
            box-shadow: 0 0 10px #FF9900;
         }

      }

      button {
         font-style: normal;
         font-weight: bold;
         font-size: 16px;
         line-height: 24px;
         color: #FFFFFF;
         background: #FF9900;
         border-radius: 5px;
         border: none;
         outline: none;
         margin-top: 20px;
         padding: 5px 10px;
      }
   }

   .exitLogin {
      position: absolute;
      width: 50px;
      height: 50px;
      right: 0;
      top: 0;
      padding: 30px;
      display: flex;
      align-items: center;
      justify-content: center;

      &:hover {
         background: rgba(255, 255, 255, 0.452);
      }

      @media screen and (max-device-width: 570px) {
         width: 20px;
         height: 20px;
      }

      span {
         position: absolute;
         display: flex;
         align-items: center;
         justify-content: center;

         &::before {
            content: "";
            position: absolute;
            width: 40px;
            height: 5px;
            background: white;
            border-radius: 50px;
            transform: rotate(45deg);
            @media screen and (max-device-width: 570px) {
               width: 30px;
               height: 3px;
            }
         }
         &::after {
            content: "";
            position: absolute;
            width: 40px;
            height: 5px;
            background: white;
            border-radius: 50px;
            transform: rotate(-45deg);
            @media screen and (max-device-width: 570px) {
              width: 30px;
              height: 3px;
            }
         }
      }
   }
}