// ========================= HOME =================================

.home {
   margin-bottom: 40px;

   &-section1 {
      position: relative;
      &__title {
         font-style: normal;
         font-weight: bold;
         font-size: 30px;
         line-height: 130%;
         text-align: center;
         color: #000000;
      }


      &-news {
         margin-top: 30px;
      }

         
      
   }

   &-section3 {
      margin-top: 50px;

       @media screen and (max-device-width: 570px) {
          margin-top: 30px;
       }

      &-row {
         display: flex;
         align-items: center;
         justify-content: space-between;

         @media screen and (max-device-width: 570px) {
            flex-direction: column;
            width: 100%;
         }

         &-block {
            text-align: center;
            @media screen and (max-device-width: 570px) {
               margin-top: 20px;
               width: 100%;
            }

            &:nth-child(1) {
               h3 {
                  img {
                     width: 50px;
                     height: 50px;
                     padding: 3px;
                     box-sizing: border-box;
                     background: rgb(0, 189, 0);
                     border-radius: 50%;
                     margin-right: 10px;
                  }
               }
            }
            &:nth-child(2) {
               h3 {
                  img {
                     width: 50px;
                     height: 50px;
                     padding: 3px;
                     box-sizing: border-box;
                     background: rgb(218, 0, 0);
                     border-radius: 50%;
                     margin-right: 10px;
                  }
               }
            }
            &:nth-child(3) {
               h3 {
                  img {
                     width: 50px;
                     height: 50px;
                     padding: 3px;
                     box-sizing: border-box;
                     border: 2px solid white;
                     border-radius: 50%;
                     margin-right: 10px;
                     margin-right: 10px;
                  }
               }
            }



           

            h3 {
               font-size: 18px;
               font-weight: bold;
               background: #ff9900;
               padding: 10px 10px;
               box-sizing: border-box;
               border-radius: 5px;
               color: white;
               margin-top: 20px;
               position: relative;
               display: flex;
               align-items: center;
               justify-content: space-between;

               @media screen and (max-device-width: 570px) {
                  font-size: 17px;
                  width: 100%;
                  justify-content: flex-start;
                  margin-top: 10px;
               }


               



            }

            h2 {
               font-size: 70px;
               font-weight: bold;

               @media screen and (max-device-width: 570px) {
                  font-size: 50px;
               }
            }

            

             
         }
      }
   }


   &-section2 {
      margin-top: 50px;

      &__title {
         font-style: normal;
         font-weight: bold;
         font-size: 30px;
         line-height: 130%;
         text-align: center;
         color: #000000;
      }
   }
}