.newsBlock{
   display: flex;
   align-items: center;
   justify-content: center;
   &-background {
      
   }

   &-front {
      position: absolute;
      background: linear-gradient(to top, rgba(0, 0, 0, 0.897), transparent);
      width: 100%;
      height: 400px;
      z-index: 150;
      display: flex;
      align-items: flex-end;

      @media screen and (max-device-width: 570px) {
         height: 400px;
      }

      h3 {
         text-align: start;
         padding: 30px 10px;
         color: white;
         @media screen and (max-device-width: 570px) {
            font-size: 15px;
            padding: 20px 10px;
            line-height: 21px;
            letter-spacing: 1px;
         }
      }
   }
}