.testBlock {
   border-top: 2px dashed black;
   padding: 20px 0;

   &-form {
      position: relative;
      .numQuestion {
         position: absolute;
         font-size: 24px;
         padding: 5px;
         box-sizing: border-box;
         border: 1px solid black;
         background: black;
         color: white;
         display: flex;
         align-items: center;
         justify-content: center;
         font-weight: bold;
         left: 0;
         top: -35px;

         @media screen and (max-device-width: 570px) {
            font-size: 17px;
            left: 0;
            top: -35px;
         }

      }

      &-question {
         width: 100%;
         text-align: start;
         margin-bottom: 25px;

         p {
            padding: 15px 20px;
            line-height: 30px;
         }

         

         
         .katex {
            font-size: 24px;
            font-family: "MathJax_Main";

            @media screen and (max-device-width: 570px) {
               font-size: 17px;
            }
         }

         
      }

      .row {
         display: flex;
         align-items: center;
         position: relative;

         
         &:hover {
            label {
               span {
                  background: white;
                  color: #FF9900;
               }

               &::before {
                  width: 100%;
                  transition: all ease 0.5s;
               }
               }
         }
         
         input {
            display: none;
            
            &:checked ~ label {
               color: white;
                &::before {
                   width: 100%;
                   transition: all ease 0.5s;
                   background: black;
                }

                span {
                   background: white;
                   color: #000000;
                }
                
               
            }
            
         }
         
         label {
            display: flex;
            align-items: center;
            font-size: 20px;
            width: 100%;
            padding: 20px;
            @media screen and (max-device-width: 570px) {
               font-size: 15px;
               padding: 13px;
            }
            
            
            span {
               display: flex;
               align-items: center;
               justify-content: center;
               border-radius: 50%;
               width: 30px;
               height: 30px;
               background: #FF9900;
               font-weight: bold;
               color: white;
               margin-right: 30px;

               @media screen and (max-device-width: 570px) {
                  width: 20px;
                  height: 20px;
                  margin-right: 15px;
               }
            }
            &::before {
               content: "";
               position: absolute;
               width: 0%;
               height: 100%;
               left: 0;
               top: 0;
               bottom: 0;
               background: #FF9900;
               z-index: -1;
               transition: all ease 0.5s;
            }
         }

      }
   }

   
}