// ==================================TestStart ==============================================

.testStart {
   .timer {
      position: fixed;
      width: 100%;
      height: 70px;
      background: rgba(0, 0, 0, 0.664);
      color: #ff990044;
      display: flex;
      align-items: center;
      justify-content: center;
      color: white;
      font-size: 25px;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 9999999;

      @media screen and (max-device-width: 570px) {
         font-size: 17px;
         height: 40px;
      }

   }

   &-footer {
      display: flex;
      justify-content: center;
      margin-bottom: 20px;

      button {
         font-size: 24px;
         background: #FF9900;
         border: 1px solid transparent;
         padding: 10px 20px;
         border-radius: 10px;
         color: white;
         animation: animeTestButton 4s infinite;

         @media screen and (max-device-width: 570px) {
            font-size: 17px;
            border-radius: 5px;

         }

         @keyframes animeTestButton {
            0% {
               background: #FF9900;
               color: white;
            }

            50% {
               background: white;
               color: black;
               border: 1px solid black;
            }

            100% {
               background: #FF9900;
               color: white;
               border: 1px solid transparent;
            }
         }
      }
   }
}



// =============================== TEST USER ==============================

.testUser {
   display: flex;
   align-items: center;
   justify-content: center;

   &-form {}
}




.testUser {
   position: fixed;
   left: 0;
   right: 0;
   top: 0;
   bottom: 0;
   display: flex;
   align-items: center;
   justify-content: center;
   z-index: 9999999;
   background: white;

   &-form {
      background: white;
      box-shadow: 0 0 10px #FF9900;
      display: flex;
      flex-direction: column;
      padding: 20px 60px;
      border-radius: 20px;

      @media screen and (max-device-width: 570px) {
         padding: 20px;
         box-shadow: none;

      }

      h2 {
         text-align: center;
         font-size: 25px;
         margin-top: 20px;

         @media screen and (max-device-width: 570px) {
            font-size: 17px;

         }

      }



      button {
         margin-top: 50px;
         padding: 5px 20px;
         background: #FF9900;
         font-size: 20px;
         border-radius: 10px;
         border: 1px solid transparent;
         color: white;
         cursor: pointer;
         transition: all ease 0.5s;

         &:disabled {
            background: gray;

            &:hover {
               background: gray;
               color: white;
               border: 1px solid transparent;
               cursor: no-drop;
            }
         }

         &:hover {
            background: white;
            color: black;
            border: 1px solid black;
            transition: all ease 0.5s;
         }
      }
   }

}


// =================================== UserNatija ==============================

.userNatija {
   h1 {
      margin: 20px 0;

      @media screen and (max-device-width: 570px) {
         font-size: 20px;
      }

      @media screen and (max-device-width: 300px) {
         font-size: 17px;
      }

   }

   .diagramma {
      margin-bottom: 20px;
   }
}


// =========================== NATIJALAR ==========================

.natijalar {

   .natija {
      &:nth-child(1) {
         h3 {
            &.natija-allBall {
               background: #5EBE73;
            }
         }
      }

      &:nth-child(2) {
         h3 {
            &.natija-allBall {
               background: #5ebe73e0;
            }
         }
      }

      &:nth-child(3) {
         h3 {
            &.natija-allBall {
               background: #5ebe73a2;
            }
         }
      }
   }

   .note {
      display: none;
      color: white;
      padding: 10px;
      box-sizing: border-box;
      border-radius: 10px;
      position: fixed;
      width: 200px;
      background-color: rgba(252, 80, 80, 0.979);
      bottom: 10px;
      right: 10px;

      @media screen and (max-device-width: 570px) {
         font-size: 12px;
         width: 181px;
      }

      &.active {
         display: flex;
      }
   }


   &-header {
      display: flex;
      align-items: center;
      justify-content: space-between;

      @media screen and (max-device-width: 570px) {
         flex-direction: column;
         justify-content: none;
      }

      h1 {
         color: #2F281E;

         @media screen and (max-device-width: 570px) {
            font-size: 17px;
         }
      }

      .formNatijalar {
         @media screen and (max-device-width: 570px) {
            margin-top: 20px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
         }



         input {
            border: 1px solid #BCD0E5;
            box-sizing: border-box;
            border-radius: 4px;
            font-size: 20px;
            padding: 5px;
            box-sizing: border-box;
            outline: none;

            @media screen and (max-device-width: 570px) {
               font-size: 17px;
            }

            &:focus {
               border: 1px solid #FF9900;
               box-shadow: 0 0 10px #FF9900;
            }
         }

         button {
            margin-left: 10px;
            font-size: 20px;
            font-style: normal;
            font-weight: 400;
            line-height: 22px;
            text-align: center;
            color: #FFFFFF;
            background: #FF9900;
            border-radius: 4px;
            padding: 5px 15px;
            box-sizing: border-box;
            border: 1px solid transparent;
            cursor: pointer;
            transition: all ease 0.5s;
            animation: anime 1s infinite;

            @keyframes anime {
               0% {}

               25% {
                  box-shadow: 0px 0px 10px #FF9900,
                     0px 0px 15px #FF9900;
               }

               100% {}
            }

            @media screen and (max-device-width: 570px) {
               font-size: 17px;
               margin-top: 15px;
            }

         }
      }

   }

   &-table {
      margin-top: 40px;


      &-top {
         display: flex;

         &__titles {
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 5px 0;

            @media screen and (max-device-width: 570px) {
               font-size: 17px;
               padding: 2px 0;
            }




            &:nth-child(1) {
               width: 5%;
               border: 2px solid black;

               @media screen and (max-device-width: 570px) {
                  width: 15%;
                  font-size: 14px;
               }
            }

            &:nth-child(2) {
               width: 35%;
               border: 2px solid black;
               border-left: none;

               @media screen and (max-device-width: 570px) {
                  width: 60%;
               }

               @media screen and (max-device-width: 300px) {
                  font-size: 12px;
               }
            }

            &:nth-child(3) {
               width: 20%;
               border: 2px solid black;
               border-left: none;


               @media screen and (max-device-width: 570px) {
                  display: none;
               }
            }

            &:nth-child(4) {
               border: 2px solid black;
               width: 20%;
               border-left: none;

               @media screen and (max-device-width: 570px) {
                  display: none;
               }
            }

            &:nth-child(5) {
               border: 2px solid black;
               width: 20%;
               border-left: none;

               @media screen and (max-device-width: 570px) {
                  width: 35%;
                  font-size: 14px;
               }

               @media screen and (max-device-width: 300px) {
                  font-size: 12px;
               }
            }

            @media screen and (max-device-width: 300px) {
               font-size: 12px;
               padding: 2px 0;
            }
         }
      }
   }
}

// =============================TEST ALL===================================

.testAll {

   .row {



      h2 {
         font-style: normal;
         font-weight: bold;
         font-size: 30px;
         line-height: 48px;
         text-align: center;

      }

      p {
         font-style: normal;
         font-weight: 600;
         font-size: 18px;
         line-height: 28px;
         text-align: center;
         // border: 3px dashed   ;
         padding: 10px;
         box-sizing: border-box;
         background: #FF9900;
         position: relative;
         overflow: hidden;
         color: white;
         box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);

         b {
            color: black;
         }

         .span {
            &:nth-child(1) {
               top: 0;
               left: 0;
               width: 100%;
               height: 2px;
               background: linear-gradient(90deg, transparent, black);
               animation: animate1 1s linear infinite;

            }

            &:nth-child(2) {
               top: -100%;
               right: 0;
               width: 2px;
               height: 100%;
               background: linear-gradient(180deg, transparent, black);
               animation: animate2 1s linear infinite;
               animation-delay: 0.25s;

            }

            &:nth-child(3) {
               bottom: 0;
               right: -100%;
               width: 100%;
               height: 2px;
               background: linear-gradient(270deg, transparent, black);
               animation: animate3 1s linear infinite;
               animation-delay: 0.5s;

            }

            &:nth-child(4) {
               bottom: -100%;
               left: 0;
               width: 2px;
               height: 100%;
               background: linear-gradient(360deg, transparent, black);
               animation: animate4 1s linear infinite;
               animation-delay: 0.75s;

            }
         }
      }



      .span {
         position: absolute;
         display: block;

         &:nth-child(1) {
            top: 0;
            left: 0;
            width: 100%;
            height: 2px;
            background: linear-gradient(90deg, transparent, white);
            animation: animate1 1s linear infinite;

         }

         @keyframes animate1 {
            0% {
               left: -100%;

            }

            50%,
            100% {
               left: 100%;
            }
         }

         &:nth-child(2) {
            top: -100%;
            right: 0;
            width: 2px;
            height: 100%;
            background: linear-gradient(180deg, transparent, white);
            animation: animate2 1s linear infinite;
            animation-delay: 0.25s;

         }

         @keyframes animate2 {
            0% {
               top: -100%;

            }

            50%,
            100% {
               top: 100%;
            }
         }

         &:nth-child(3) {
            bottom: 0;
            right: -100%;
            width: 100%;
            height: 2px;
            background: linear-gradient(270deg, transparent, white);
            animation: animate3 1s linear infinite;
            animation-delay: 0.5s;

         }

         @keyframes animate3 {
            0% {
               right: -100%;

            }

            50%,
            100% {
               right: 100%;
            }
         }

         &:nth-child(4) {
            bottom: -100%;
            left: 0;
            width: 2px;
            height: 100%;
            background: linear-gradient(360deg, transparent, white);
            animation: animate4 1s linear infinite;
            animation-delay: 0.75s;

         }

         @keyframes animate4 {
            0% {
               bottom: -100%;

            }

            50%,
            100% {
               bottom: 100%;
            }
         }

      }

   }

   .testAllform {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 20px;

      @media screen and (max-device-width: 570px) {
         width: 100%;
      }

      margin-bottom: 40px;

      .row {
         display: flex;
         flex-direction: column;
         margin-top: 20px;

         @media screen and (max-device-width: 570px) {
            width: 100%;
         }

         label {
            font-weight: bold;
         }

         select {
            margin-top: 10px;
            font-size: 20px;
            width: 400px;
            padding: 10px 20px;
            border-radius: 10px;
            box-shadow: inset 0 1px 1px rgb(0 0 0 / 8%);
            outline: none;

            &:focus {
               box-shadow: 0 0 10px #FF9900;
            }

            @media screen and (max-device-width: 570px) {
               width: 100%;
               font-size: 15px;
            }
         }

         &-items {
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 400px;
            padding: 10px 20px;
            box-sizing: border-box;
            background: #fa960069;

            @media screen and (max-device-width: 570px) {
               width: 100%;

               h3 {
                  font-size: 15px;
               }
            }

            &:nth-child(2) {
               border-top: 2px dashed black;
            }
         }

      }

      button {
         font-size: 20px;
         margin-top: 20px;
         padding: 10px 20px;
         box-sizing: border-box;
         border-radius: 5px;
         border: 1px solid transparent;
         background: #FF9900;
         color: white;
         transition: all ease 0.5s;
         cursor: pointer;

         &:hover {
            background: white;
            border: 1px solid black;
            color: black;
            transition: all ease 0.5s;
         }

         &:disabled {
            background: gray;

            &:hover {
               background: gray;
               color: white;
               border: 1px solid transparent;
               cursor: no-drop;

            }
         }

         @media screen and (max-device-width: 570px) {
            font-size: 15px;
         }
      }
   }
}


// ============================= QABUL FORM ============================

.qabulForm {
   margin-bottom: 50px;

   .row {
      h2 {
         text-align: center;
         font-size: 30px;

         @media screen and (max-device-width: 570px) {
            font-size: 25px;
         }
      }

      h3 {
         text-align: center;
         margin-top: 20px;
         border: none;
         border-radius: 50px;
         background: #FF9900;
         color: white;
         padding: 20px 0;
         box-sizing: border-box;

         @media screen and (max-device-width: 570px) {
            font-size: 15px;
            padding: 10px 0;
         }

      }

      p {
         margin-top: 30px;
         padding: 20px 10px;
         border: 3px dashed black;
         text-align: center;
         font-weight: bold;
      }
   }


   &-inner {
      margin-top: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      @media screen and (max-device-width: 570px) {
         margin-top: 20px;
      }

      .row {
         display: flex;
         flex-direction: column;
         justify-content: center;
         text-align: start;
         margin-top: 20px;
         @media screen and (max-device-width: 570px) {
            width: 100%;
         }

         label {
            font-size: 24px;
            font-weight: bold;
            width: 100%;
            @media screen and (max-device-width: 570px) {
               font-size: 15px;
            }
         }

         input {
            margin-top: 10px;
            font-size: 24px;
            width: 100%;
            border-radius: 10px;
            box-shadow: inset 0 1px 1px rgb(0 0 0 / 8%);
            border: 1px solid black;
            outline: none;
            padding: 10px 20px;
            box-sizing: border-box;
            @media screen and (max-device-width: 570px) {
               font-size: 17px;
               padding: 10px 10px;
               width: 100%;
            }

            &:focus {
               box-shadow: 0 0 10px #FF9900;
            }
         }

      }
      button {
         font-size: 24px;
         margin-top: 30px;
         padding: 10px 20px;
         background: #FF9900;
         border: 1px solid transparent;
         color: white;
         border-radius: 5px;  
         transition: all ease 0.5s;

         @media screen and (max-device-width: 570px) {
            font-size: 15px;
            
         }
         
         &:hover {
            background: white;
            border: 1px solid black;
            color: black;
            transition: all ease 0.5s;
            cursor: pointer;
         }

         &:disabled {
            background: gray;

            &:hover {
               background: gray;
               color: white;
               border: 1px solid transparent;
               cursor: no-drop;
               
            }
         }
      }
   }
}