.homeS2Block {
   display: flex;
   flex-direction: column;
   min-width: 270px;
   max-width: 270px;
   align-items: center;
   background: #FFFFFF;
   box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.2);
   border-radius: 5px;
   margin: 0 20px;
   margin-top: 40px;
   padding: 15px;
   text-align: center;
   transition: all ease 0.5s;

   @media screen and (max-device-width: 570px) {
      min-width: 200px;
      max-width: 200px;
      margin-top: 25px;
      
   }

   &:hover {
      box-shadow: 0px 12px 21px rgba(0, 0, 0, 0.1);
      transition: all ease 0.5s;
   }

   &:active {
   }

   &-img {

      img {
         width: 200px;
         height: 130px;
      }
   }

   &-info {
      margin-top: 20px;
      width: 250px;
      @media screen and (max-device-width: 570px) {
         width: 180px;

      }
      
      h2 {
         text-align: center;
      } 
      
      span {
         font-style: normal;
         font-weight: normal;
         font-size: 16px;
         line-height: 24px;
         text-align: center;
         color: #000000;
         overflow-wrap: break-word;
      }  
   }
}