

body {
   margin: 0;
   padding: 0;
   font-family: sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
a {
   margin: 0;
   padding: 0;
}


.container {
   max-width: 1140px;
   width: 100%;
   margin: 0 auto;
   padding: 0 15px;
   box-sizing: border-box;
}

.header {
   position: fixed;
   width: 100%;
   margin: 0 auto;
   top: 0;
   left: 0;
   right: 0;
   background: #fff;
   box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.05);
   box-sizing: border-box;
   z-index: 10000000;

   &-inner {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 10px 15px;
      position: relative;
      box-sizing: border-box;
      
      @media screen and (max-device-width: 570px) {
         max-width: 570px;
         width: 100%;
         padding: 10px 8px;
         box-sizing: border-box;
         margin: 0 auto;
      }
      
      
      
      
      &__logo {
         box-sizing: border-box;
         
         img {
            width: 160px;
            height: 50px;
            box-sizing: border-box;
            @media screen and (max-device-width: 570px) {
               width: 130px;
               height: 40px;
               box-sizing: border-box;
            }
         }
      }
      
      &-items {
         display: flex;
         box-sizing: border-box;
         @media screen and (max-device-width: 570px) {
            position: fixed;
            align-items: center;
            flex-direction: column;
            top: 0;
            right: 0;
            height: 100vh;
            width: 0%;
            overflow: hidden;
            padding-top: 30px;
            background: #FF9900;         
            transition: all ease 0.5s;
            
            &.active {
               width: 50%;
            }
         }

         &__links {
            text-decoration: none;
            padding: 0 10px;
            margin: 0 10px;
            font-style: normal;
            font-weight: normal;
            font-size: 15px;
            line-height: 28px;
            color: #2F281E;
            position: relative;

            @media screen and (max-device-width: 570px) {
               margin-top: 20px;
               color: white;
            }


            &::before {
               content: "";
               position: absolute;
               width: 8px;
               height: 8px;
               border-left: 3px solid #2F281E;
               border-top: 3px solid #2F281E;
               top: -2px;
               left: 5px;
               opacity: 0;
               transition: all ease 0.5s;
            }

            &::after {
               content: "";
               position: absolute;
               width: 8px;
               height: 8px;
               border-right: 3px solid #2F281E;
               border-bottom: 3px solid #2F281E;
               bottom: -2px;
               right: 5px;
               opacity: 0;
               transition: all ease 0.5s;
            }

            &:hover {

               &::before {
                  top: -5px;
                  left: 0px;
                  opacity: 1;
                  transition: all ease 0.5s;
               }

               &::after {
                  bottom: -5px;
                  right: 0px;
                  opacity: 1;
                  transition: all ease 0.5s;
               }
            }
         }

         &__login {
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 22px;
            text-align: center;
            color: #FFFFFF;
            background: #FF9900;
            border-radius: 4px;
            padding: 5px 15px;
            box-sizing: border-box;
            border: 1px solid transparent;
            cursor: pointer;
            transition: all ease 0.5s;

            @media screen and (max-device-width: 570px) {
               font-size: 17px;
               padding: 5px 0;
               width: 70px;
              box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
              margin-top: 40px;
              color: black;
              background: white;
            
              animation: anime 1s infinite;

              @keyframes anime {
                 0%{
                  
                 }25% {
                  box-shadow: 0px 0px 10px rgba(255, 255, 255, 0.808),
                              0px 0px 20px rgba(255, 255, 255, 0.808);
                 }100% {

                 }
              }
            }

            &:hover {
               color: #2F281E;
               background-color: #fff;
               border: 1px solid #2F281E;
               transition: all ease 0.5s;
            }
         }
      }

      .burger {
         
         position: relative;
         width: 30px;
         height: 20px;
         display: none;
         align-items: center;
         justify-content: center;
         cursor: pointer;
         box-sizing: border-box;

         &.active {
            span {
               height: 0;


               &::before {
                  transform: translateY(6px) rotate(45deg);
               }

               &::after {
               transform: translateY(-6px) rotate(-45deg);
               }
            }
         }

         @media screen and (max-device-width: 570px) {
            display: flex;
         }
         span {
            position: absolute;
            width: 30px;
            height: 3px;
            border-radius: 50px;
            background: #000;
            
            &::before {
               content: "";
               position: absolute;
               width: 30px;
               height: 3px;
               background: #000;
               top: -8px;
               border-radius: 50px;
               transition: all ease 0.5s;
            }  
            
            &:after {
               content: "";
               position: absolute;
               width: 30px;
               height: 3px;
               background: #000;
               bottom: -8px;
               border-radius: 50px;
               
               transition: all ease 0.5s;
            }
         }
      }
   }
}

.content {
   margin-top: 90px;
   margin-bottom: 50pxpx;
   @media screen and (max-device-width: 570px) {
      margin-bottom: 40px;
   }
}

.footer {
   width: 100%;
   margin: 0 auto;
   left: 0;
   right: 0;
   background: #FFFFFF;
   box-sizing: border-box;
   
   &-inner {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 10px 15px;
      box-sizing: border-box;
      
      
      
      &__logo {
         box-sizing: border-box;
         
         img {
            width: 160px;
            height: 50px;
            box-sizing: border-box;
            
         }
         @media screen and (max-device-width: 570px) {
            display: none;
            width: 0;
            height: 0;
         }
      }
      
      &-items {
         box-sizing: border-box;
         span {
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 26px;
            color: #C6C6C6;
            box-sizing: border-box;

            @media screen and (max-device-width: 570px) {
               font-size: 8px;
               text-align: start;
            }


         }
      }
   }
}